                                    ref="currentPlaceInput2"
                                    placeholder="Ingrese el nombre de un lugar"
                                    @place_changed="usePlace3"
                                >
                                </gmap-autocomplete>
                                </div>
                            </div>
                            <div class="v-text-field__details">
                                <div class="v-messages theme--light">
                                <div class="v-messages__wrapper"></div>
