<script>
import { Plugins } from "@capacitor/core";
import { Device } from '@capacitor/device';
import { Camera, CameraResultType } from '@capacitor/camera';
import { Directory, Encoding} from '@capacitor/filesystem';
import { Geolocation } from '@capacitor/geolocation';

const {Filesystem}=Plugins;

import {Bar, Pie} from 'vue-chartjs';
import { Chart as ChartJS, Title, Tooltip, Legend, BarElement, ArcElement, CategoryScale, LinearScale } from 'chart.js'
ChartJS.register(Title, Tooltip, Legend, BarElement, ArcElement, CategoryScale, LinearScale)

const ioservice=require('./services/socketio.service').service();
const axios=require('axios').default;

function blobToBase64(blob) {
    return new Promise((resolve, _) => {
        console.log(_);
        const reader = new FileReader();
        reader.onloadend = () => resolve(reader.result);
        reader.readAsDataURL(blob);
    });
}

function getItem(fieldName, array){
    for(let index in array){
        if (array[index]['types'][0] === fieldName){
            return array[index]['long_name'];
        }
    }
}

export default {
    name: 'App',
    components:{
        Bar,
        Pie
    },
    data: ()=>({
        isMobile: false,
        drawer: false,
        welcomeDialogue: false,
        loginDialogue: false,
        infoDialogue: false,
        fullInfoDialogue: false,
        registerReportDialogue: false,
        isLogin: false,
        msg:{
          content: '',
          has: false,
          icon: 'mdi-information',
          color: 'red'
        },
        user:{
            username: '', place: '', role: '', avatar: '', settings:{
                isPrivate: null,
                newAlerts: null,
                sound: null
            }
        },
        registerData: {
            username: '', dni: '', place: '', codeVerification: 0, email: '', password: ''
        },
        loginData: {
            username: '', oldPassword: '', password: '', newPassword: ''
        },
        
        menu:[
            {icon: 'mdi-cog', title: 'Configuracion', path: '/config', action: 'openConfigDialogue', args: []},
            {icon: 'mdi-forum', title: 'Reportes', path: '/my-reports', action: 'openReportsDialogue'}
        ],
        menu2:[
            {title: 'Centro de ayuda', action: ''},
            {title: 'Informar de un problema', action: ''},
            {title: 'Acerca de', action: ''}
        ],
        incidences:[
            {name: 'Violencia Familiar', icon: 'mdi-home'},
            {name: 'Delincuencia', icon: 'mdi-robber'},
            {name: 'Accidente de Tránsito', icon: 'mdi-car'},
            {name: 'Otro', icon: 'mdi-pencil-circle'}
        ],
        report:{
            id: '',
            author: '',
            description: '',
            category: '', 
            photo: '',
            datetime: '',
            address: '',
            locality: '',
            position: {lat: -1, lng: -1},
            show: true
        },
        defaultReport:{
            id: '',
            author: '',
            description: '',
            category: '',
            photo: '',
            datetime: '',
            address: '',
            locality: '',
            position: {lat: -1, lng: -1},
            show: true
        },
        selected: false,

        myImg: null,
        logo: require('@/assets/logo1.png'),
        banner: require('@/assets/banner.webp'),
        users: [],
        userRole: null,
        map: null,
        currentAddress: '',
        reports:[],
        currentReports:[],
        currentReport: {
            author: 'Anonymous',
            description: '',
            category: '',
            photo: '',
            datetime: '',
            address: '',
            locality: '',
            position: {lat: -1, lng: -1},
            show: true
        },
        center: null,
        coords: {lat: -1, lng: -1},
        currentPlace: null,
        currentCategory: '',
        token: null,

        openModalChangePassword: false,
        openModalChangePlace: false,
        deleteDialogue: false,
        updateDialogue: false,
        currentDeleteAction: null,
        currentIndex: -1,
        currentID: -1,
        myRoute: {
            path: '/login'
        },
        overallDialogue: {
            show: false,
            title: 'Mis reportes'
        },
        settings: [],

        chartData: {
            labels: ['Violencia Familiar', 'Delincuencia', 'Accidente de Tránsito','Otro'],
            datasets: [
                {
                    label: '',
                    backgroundColor: '',
                    data: []
                }
            ],
        },
        chartData2: {
            labels: [],
            datasets: [
                {
                    backgroundColor: [],
                    data: []
                }
            ]
        }
    }),
    methods:{
        apply(action, ...args){
            this[action](args);
        },
        navegateTo(path){
            if (this.myRoute.path !== path){
                this.myRoute.path=path;
            }
        },
        newMessage(content){
          this.msg.has=true;
          this.msg.content=content.msg;
          this.msg.icon=content.icon;
          this.msg.color=content.color;
        },
        async getLocation(){
            const position=await Geolocation.getCurrentPosition({timeout: 60000, enableHighAccuracy: true});
            let latitude = position.coords.latitude;
            let longitude = position.coords.longitude;
            const COORDS={lat:latitude, lng: longitude};

            this.coords=Object.assign({}, this.coords, COORDS);
        },
        getCurrentPlace(){
            if (this.coords.lat === -1 && this.coords.lng === -1){
                return this.newMessage({msg: 'No se pudo obtener la ubicacion actual. Por favor, verifica que el aplicativo tenga acceso al GPS.', icon: 'mdi-information', color: 'red'});
            }
            const URI_API=`https://maps.googleapis.com/maps/api/geocode/json?latlng=${this.coords.lat},${this.coords.lng}&key=AIzaSyDI0JkI5057VYaAZEU9GAfK301sk3mFgXo`;
            axios.get(URI_API).then((resp)=>{
                var address=getItem('sublocality_level_1', resp.data.results[1].address_components); 
                if (!address){
                    address=getItem('locality', resp.data.results[1].address_components); 
                }
                const PLACE=getItem('administrative_area_level_2', resp.data.results[1].address_components);    
                this.registerData.place=PLACE;
                this.$refs.currentPlaceInput.$el.value=address + ' - '+PLACE;
                
                this.currentPlace={
                    formatted_address: address,
                    address_components: resp.data.results[0].address_components
                };
                this.report.position=this.coords;
                this.newMessage({msg: 'Ubicacion encontrada.', icon: 'mdi-check', color: 'green'});
            }).catch((error)=>{
                this.newMessage({msg: error.response.data.error_message, icon: 'mdi-information', color: 'red'});
            });
        },
        getCurrentPlace2(){
            const URI_API=`https://maps.googleapis.com/maps/api/geocode/json?latlng=${this.coords.lat},${this.coords.lng}&key=AIzaSyDI0JkI5057VYaAZEU9GAfK301sk3mFgXo`;
            axios.get(URI_API).then((resp)=>{
                const PLACE=getItem('administrative_area_level_2', resp.data.results[0].address_components);

                this.$refs.currentPlaceInput2.$el.value=PLACE;
                this.user.place=PLACE;
                this.newMessage({msg: 'Ubicacion encontrada.', icon: 'mdi-check', color: 'green'});
            }).catch((error)=>{
                this.newMessage({msg: error.response.data.error_message, icon: 'mdi-information', color: 'red'});
            });
        },

        getFullLocation(){
            this.getLocation();

            setTimeout(()=>{
                this.getPlaceLocation();
            }, 2000);
        },
        getPlaceLocation(){
            if (this.coords.lat === -1 && this.coords.lng === -1){
                return this.newMessage({msg: 'No se pudo obtener la ubicacion actual. Por favor, verifica que el aplicativo tenga acceso al GPS.', icon: 'mdi-information', color: 'red'});
            }
            const URI_API=`https://maps.googleapis.com/maps/api/geocode/json?latlng=${this.coords.lat},${this.coords.lng}&key=AIzaSyDI0JkI5057VYaAZEU9GAfK301sk3mFgXo`;
            axios.get(URI_API).then((resp)=>{
                const PLACE=getItem('administrative_area_level_2', resp.data.results[2].address_components);
                this.registerData.place=PLACE;

                axios.get(process.env.VUE_APP_SOCKET_ENDPOINT+'/api/v1/report', {params: {token: this.token, locality: PLACE}}).then((resp)=>{
                    const rawReports=resp.data.data.reports;
                    var reports=[];
                    for (let index in rawReports){
                        reports[index]=JSON.parse(JSON.stringify({}));
                        reports[index].id=new String(Math.random() * 100).valueOf();
                        reports[index].id2=new String(Math.random() * 100).valueOf();
                        reports[index].address=rawReports[index].address;
                        reports[index].position=rawReports[index].position;
                    }
                    this.reports=reports;
                }).catch((error)=>{
                    this.newMessage({msg: error.response.data.message, icon: 'mdi-information', color: 'red'});
                });  
                this.newMessage({msg: 'Ubicacion encontrada.', icon: 'mdi-check', color: 'green'});
            }).catch((error)=>{
                this.newMessage({msg: error.response.data.error_message, icon: 'mdi-information', color: 'red'});
            });
        },
        async takePicture() {
            const image = await Camera.getPhoto({
                quality: 95,
                resultType: CameraResultType.DataUrl
            });

            var imageUrl = image.dataUrl;
            // Can be set to the src of an image now
            this.report.photo = imageUrl;
        },
        usePlace(_place) {
            this.currentPlace=_place;
            this.coords={
                lat: this.currentPlace.geometry.location.lat(),
                lng: this.currentPlace.geometry.location.lng(),
            };

            const locality=getItem('administrative_area_level_2', this.currentPlace.address_components);
            axios.get(process.env.VUE_APP_SOCKET_ENDPOINT+'/api/v1/report', {params: {token: this.token, locality: locality}}).then((resp)=>{
                const rawReports=resp.data.data.reports;
                var reports=[];
                for (let index in rawReports){
                    reports[index]=JSON.parse(JSON.stringify({}));
                    reports[index].id=new String(Math.random() * 100).valueOf();
                    reports[index].id2=new String(Math.random() * 100).valueOf();
                    reports[index].address=rawReports[index].address;
                    reports[index].position=rawReports[index].position;
                }
                this.reports=reports;
            }).catch((error)=>{
                this.newMessage({msg: error.response.data.message, icon: 'mdi-information', color: 'red'});
            });         
        },
        usePlace2(_place) {
            this.currentPlace=_place;
            this.coords={
                lat: this.currentPlace.geometry.location.lat(),
                lng: this.currentPlace.geometry.location.lng(),
            };
            this.report.position=this.coords;
        },
        usePlace3(_place) {
            const PLACE=getItem('administrative_area_level_2', _place.address_components);
            this.$refs.currentPlaceInput2.$el.value=PLACE;
            this.user.place=PLACE;
        },
        customFilter(item, queryText) {
          const textOne = item.description.toLowerCase();
          const searchText = queryText.toLowerCase()

          const index=this.currentReports.indexOf(item);
          if (textOne.indexOf(searchText) > -1){
            this.currentReports[index].show=true;
          }else{
            this.currentReports[index].show=false;
          } 
          return textOne.indexOf(searchText) > -1;
        },

        //
        logIn(){
            axios.get(process.env.VUE_APP_SOCKET_ENDPOINT+'/api/v1/login', {params: this.loginData}).then((resp)=>{
                this.newMessage({msg: resp.data.message, icon: 'mdi-check', color: 'green'});

                this.token=resp.data.data.token;
                this.user=resp.data.data.user;
                this.user.avatar=process.env.VUE_APP_SOCKET_ENDPOINT+this.user.avatar;
                localStorage.setItem('uid', this.token);
                this.isLogin=true;
                setTimeout(()=>{
                    this.loginDiaologue=false;
                    window.location.reload();
                }, 2000);
            }).catch((error)=>{
                this.newMessage({msg: error.response.data.message, icon: 'mdi-information', color: 'red'});
            });
        },
        logOut(){    
            axios.get(process.env.VUE_APP_SOCKET_ENDPOINT+'/api/v1/logout', {params: {token: this.token}}).then(()=>{
                localStorage.removeItem('uid');
                this.token=null;
                this.isLogin=false;
                window.location.reload();
            }).catch((error)=>{
                this.newMessage({msg: error.response.data.message, icon: 'mdi-information', color: 'red'});
            });
        },
        createAccount(){
            axios.post(process.env.VUE_APP_SOCKET_ENDPOINT+'/api/v1/register', this.registerData).then((resp)=>{
                this.newMessage({msg: resp.data.message, icon: 'mdi-check', color: 'green'});

                setTimeout(()=>{
                    this.navegateTo('/login');
                }, 2100);
            }).catch((error)=>{
                this.newMessage({msg: error.response.data.message, icon: 'mdi-information', color: 'red'});
            });
        },
        recoverAccount(){
            axios.post(process.env.VUE_APP_SOCKET_ENDPOINT+'/api/v1/forgot', this.loginData).then((resp)=>{
                this.newMessage({msg: resp.data.message, icon: 'mdi-check', color: 'green'});

                setTimeout(()=>{
                    this.navegateTo('/login');
                }, 2100);
            }).catch((error)=>{
                this.newMessage({msg: error.response.data.message, icon: 'mdi-information', color: 'red'});
            });
        },
        resetAccount(){
            if (this.loginData.password.trim() === '' || this.loginData.newPassword.trim() === ''){
                return this.newMessage({msg: 'Complete los campos.', icon: 'mdi-information', color: 'red'});
            }
            if (this.loginData.password === this.loginData.newPassword){
                axios.get(process.env.VUE_APP_SOCKET_ENDPOINT+'/api/v1/recover/'+this.token, {params: this.loginData}).then((resp)=>{
                    this.newMessage({msg: resp.data.message, icon: 'mdi-check', color: 'green'});

                    setTimeout(()=>{
                        window.location.href='/';
                        this.navegateTo('/login');
                    }, 2100);
                }).catch((error)=>{
                    this.newMessage({msg: error.response.data.message, icon: 'mdi-information', color: 'red'});
                });                
            }else{
                this.newMessage({msg: 'Las contraseñas no coinciden.', icon: 'mdi-information', color: 'red'});
            }
        },

        //
        openSignForm(path){
            if (!this.loginDialogue){
                this.loginDialogue=true;  
            }
            this.navegateTo(path);
        },
        openRegisterIncidence(){
            if (this.token){
              this.registerReportDialogue=true;
            }else{
              this.loginDialogue=true;
            }    
        },
        registerIncidence(){
            if (this.token){
                var hasPosition=false;
                this.report.id=new String(Math.random() * 100).valueOf();
                this.report.id2=new String(Math.random() * 100).valueOf();
                this.report.author=this.user.settings.isPrivate? 'Anonymous':this.user.username;
                this.report.category=this.report.category.name;
                this.report['datetime']=new Date().toISOString();
                this.report['address']=this.currentPlace.formatted_address;
                this.report['locality']=getItem('administrative_area_level_2', this.currentPlace.address_components);

                for (let index in this.reports){
                    let position=this.reports[index].position;
                    if (position.lat === this.coords.lat && position.lng === this.coords.lng){
                        hasPosition=true;
                        break;
                    }
                }
                if (!hasPosition){
                    let newReport={id: new String(Math.random() * 100).valueOf(), id2: new String(Math.random() * 100).valueOf(), address:  this.currentPlace.formatted_address, position: this.coords};
                    this.reports.push(newReport);
                }

                ioservice.socket.emit('new-report', this.report);
                this.$refs.currentPlaceInput.$el.value='';
                this.report=JSON.parse(JSON.stringify(this.defaultReport));
                this.registerReportDialogue=false;
            }

        },
        showReports(index){
            this.infoDialogue=true;
            this.currentAddress=this.reports[index].address;

            const position=this.reports[index].position;
            axios.get(process.env.VUE_APP_SOCKET_ENDPOINT+'/api/v1/report', {params: {token: this.token, lng: position.lng, lat: position.lat }}).then((resp)=>{
                const matrix={
                    'Violencia Familiar': 'mdi-home',
                    'Delincuencia': 'mdi-robber',
                    'Accidente de Tránsito': 'mdi-car',
                    'Otro': 'mdi-pencil-circle'

                };
                var raw=resp.data.data.reports;
                var reports=[];
                for (let index in raw){
                    var c=raw[index].category;
                    reports[index]=raw[index];
                    reports[index]['category']={name: c, icon: matrix[c]};
                    reports[index]['datetime']=raw[index].createAt;
                    reports[index]['show']=true;
                }
                this.currentReports=reports;
            }).catch((error)=>{
                this.newMessage({msg: error.response.data.message, icon: 'mdi-information', color: 'red'});
            });  
        },
        showFullIncident(index){
          this.currentReport=this.currentReports[index];
          this.fullInfoDialogue=true;
        },

        //
        searchByCategory(){
          for (let index in this.currentReports){
           
            if (this.currentReports[index].category.name === this.currentCategory.name){
              this.currentReports[index].show=true;
            }else{
              this.currentReports[index].show=false;
            }
          }
        },

        // MENU
        openConfigDialogue(){
            this.drawer=false;
            this.overallDialogue.show=true;
            this.overallDialogue.title='Configuracion';
            this.myRoute.path='/config';
        },
        openReportsDialogue(){
            axios.get(process.env.VUE_APP_SOCKET_ENDPOINT+'/api/v1/report', {params: {token: this.token}}).then((resp)=>{
                const matrix={
                    'Violencia Familiar': 'mdi-home',
                    'Delincuencia': 'mdi-robber',
                    'Accidente de Tránsito': 'mdi-car',
                    'Otro': 'mdi-pencil-circle'

                };
                var raw=resp.data.data.reports;
                var reports=[];
                for (let index in raw){
                    var c=raw[index].category;
                    reports[index]=raw[index];
                    reports[index]['category']={name: c, icon: matrix[c]};
                    reports[index]['datetime']=raw[index].createAt;
                    reports[index]['show']=true;
                }
                this.currentReports=reports;

                this.drawer=false;
                this.overallDialogue.title="Reportes";
                this.overallDialogue.show=true;
                this.myRoute.path="/reports";
            }).catch((error)=>{
                this.newMessage({msg: error.response.data.message, icon: 'mdi-information', color: 'red'});
            });
        },
        openStaticsDialogue(){
            axios.get(process.env.VUE_APP_SOCKET_ENDPOINT+'/api/v1/report', {params: {token: this.token, locality: this.user.place, count: true}}).then((resp)=>{
                const colors=['#0D47A1', '#009688', '#AEEA00', '#FFAB00', '#41B883', '#E46651', '#00D8FF', '#DD1B16', '#673AB7', '#BF360C', '#795548'];
                const data=resp.data.data;
                const raw=data.reports;
                const _raw=data.reports2;

                this.chartData2.labels=[];
                this.chartData2.datasets[0].data=[];
                for (let index in _raw){
                    if (_raw[index]._id === this.user.place){
                        this.chartData.datasets[0].backgroundColor=colors[index];
                    }
                    this.chartData2.labels.push(_raw[index]._id);
                    this.chartData2.datasets[0].backgroundColor.push(colors[index]);
                    this.chartData2.datasets[0].data.push(_raw[index].count);
                }

                let raw2={};
                for (let index in raw){
                    raw2[raw[index]._id]=raw[index].count;
                }
                this.chartData.datasets[0].label=this.user.place;
                this.chartData.datasets[0].data=[
                    raw2['Violencia Familiar'] || 0, 
                    raw2['Delincuencia'] || 0, 
                    raw2['Accidente de Tránsito'] || 0,
                    raw2['Otro'] || 0
                ];

                this.drawer=false;
                this.overallDialogue.title="Estadisticas";
                this.overallDialogue.show=true;
                this.myRoute.path="/statics";
            }).catch((error)=>{
                this.newMessage({msg: error.response.data.message, icon: 'mdi-information', color: 'red'});
            });
        },
        openUsersDialogue(){
            axios.get(process.env.VUE_APP_SOCKET_ENDPOINT+'/api/v1/users', {params: {token: this.token}}).then((resp)=>{
                this.users=resp.data.data.users;

                this.drawer=false;
                this.overallDialogue.title="Usuarios";
                this.overallDialogue.show=true;
                this.myRoute.path="/users";
            }).catch((error)=>{
                this.newMessage({msg: error.response.data.message, icon: 'mdi-information', color: 'red'});
            });
        },

        // ACTIONS OF MAIN MENU
        saveConfig(){
            const data={token: this.token, place: this.user.place ,newSettings: this.user.settings};
            axios.put(process.env.VUE_APP_SOCKET_ENDPOINT+'/api/v1/user/config', data).then((resp)=>{
                this.newMessage({msg: resp.data.message, icon: 'mdi-check', color: 'green'});  
                window.location.reload();  

            }).catch((error)=>{
                this.newMessage({msg: error.response.data.message, icon: 'mdi-information', color: 'red'});   
            });
        },
        async downloadExcel(){
            const info = await Device.getInfo();
            const today = new Date();
            const date = today.getFullYear() + "-" + (today.getMonth() + 1) + "-" +today.getDate();
            const fileName = "reports" + "_" + date + ".xlsx";
            
            const response=await axios.get(process.env.VUE_APP_SOCKET_ENDPOINT+'/api/v1/report', {params: {token: this.token, format: 'excel'}, responseType: 'blob'}); 
            if(info && ['android', 'ios'].includes(info.platform)){ 
                const base64=await blobToBase64(response.data);
                try {
                    const response2=await Filesystem.writeFile({
                        path: 'Download/'+fileName,
                        data: base64,
                        directory: Directory.ExternalStorage,
                        encoding: Encoding.UTF8
                    });
                    
                    this.newMessage({msg: 'Archivo Guardado en '+response2.uri, icon: 'mdi-check', color: 'green'});
                } catch(e) {
                    this.newMessage({msg: e, icon: 'mdi-information', color: 'red'});
                }
            }else{
                const blob = new Blob([response.data], { type: "text/csv" });
                const url = window.URL.createObjectURL(blob);
                const link = document.createElement("a");
                link.href = url;
                link.setAttribute("download", fileName);

                document.body.appendChild(link);
                link.click();

                window.URL.revokeObjectURL(url);
                document.body.removeChild(link);
            }
                    
        },

        changePassword(){
            if (this.loginData.oldPassword.trim() === '' || this.loginData.password.trim() === '' || this.loginData.newPassword.trim() === ''){
                return this.newMessage({msg: 'Complete los campos.', icon: 'mdi-information', color: 'red'});
            }
            if (this.loginData.password === this.loginData.newPassword){
                axios.get(process.env.VUE_APP_SOCKET_ENDPOINT+'/api/v1/change-password/'+this.token, {params: this.loginData}).then((resp)=>{
                    this.newMessage({msg: resp.data.message, icon: 'mdi-check', color: 'green'});

                    setTimeout(()=>{
                        localStorage.removeItem('uid');
                        window.location.href='/';
                        this.navegateTo('/login');
                    }, 2100);
                }).catch((error)=>{
                    this.newMessage({msg: error.response.data.message, icon: 'mdi-information', color: 'red'});
                });                
            }else{
                this.newMessage({msg: 'Las contraseñas no coinciden.', icon: 'mdi-information', color: 'red'});
            }
        },
        deleteMyReport(...args){
            this.deleteDialogue=true;
            this.currentDeleteAction=this.deleteMyReportConfirm;
            this.currentIndex=args[0];
            this.currentID=args[1];
        },
        deleteMyReportConfirm(){
            axios.delete(process.env.VUE_APP_SOCKET_ENDPOINT+'/api/v1/report', {params: {token: this.token, reportID: this.currentID}}).then(()=>{
                this.currentReports.splice(this.currentIndex, 1);

                this.deleteDialogue=false;
                this.currentIndex=-1;
                this.currentID=-1;                
            }).catch((error)=>{
                this.newMessage({msg: error.response.data.message, icon: 'mdi-information', color: 'red'});
            });
        },

        updateUser(index, userID){
            this.updateDialogue=true;
            this.currentIndex=index;
            this.currentID=userID;
            this.userRole=this.users[this.currentIndex].role;
        },
        updateUserConfirm(){
            const data={token: this.token, userID: this.currentID, newRole: this.userRole};
            axios.put(process.env.VUE_APP_SOCKET_ENDPOINT+'/api/v1/user', data).then((resp)=>{
                this.newMessage({msg: resp.data.message, icon: 'mdi-check', color: 'green'});
                this.users[this.currentIndex].role=this.userRole;

                this.updateDialogue=false;
                this.currentIndex=-1;
                this.currentID=-1;                
            }).catch((error)=>{
                this.newMessage({msg: error.response.data.message, icon: 'mdi-information', color: 'red'});
                this.updateDialogue=false;
                this.currentIndex=-1;
                this.currentID=-1;      
            });
        },
        deleteUser(index, userID){
            this.deleteDialogue=true;
            this.currentDeleteAction=this.deleteUserConfirm;
            this.currentIndex=index;
            this.currentID=userID;
        },
        deleteUserConfirm(){
            axios.delete(process.env.VUE_APP_SOCKET_ENDPOINT+'/api/v1/user', {params: {token: this.token, userID: this.currentID}}).then(()=>{
                this.users.splice(this.currentIndex, 1);

                this.deleteDialogue=false;
                this.currentIndex=-1;
                this.currentID=-1;                
            }).catch((error)=>{
                this.newMessage({msg: error.response.data.message, icon: 'mdi-information', color: 'red'});
                this.deleteDialogue=false;
                this.currentIndex=-1;
                this.currentID=-1;      
            });
        },

    },
    mounted(){
        this.getFullLocation();
        const URI=window.location.pathname.split('/');
        if ((URI[1] === 'recover') && URI[2] !== undefined){
            this.myRoute.path='/'+URI[1];
            this.token=URI[2];
            this.loginDialogue=true;
        }else if (URI[1] === 'save' && URI[2] !== undefined){
            axios.get(process.env.VUE_APP_SOCKET_ENDPOINT+'/api/v1/save/'+URI[2]).then((resp)=>{
                this.newMessage({msg: resp.data.message, icon: 'mdi-check', color: 'green'});

                setTimeout(()=>{
                    window.location.href='/';
                    this.welcomeDialogue=true;
                    this.navegateTo('/login');
                }, 2100);
            }).catch((error)=>{
                this.newMessage({msg: error.response.data.message, icon: 'mdi-information', color: 'red'});
            });        
        }
    },
    beforeUnmount(){
        ioservice.disconnect();
    },
    created(){
        this.isMobile= window.screen.availWidth < 1200;
        this.token=localStorage.getItem('uid');
        if (this.token){
            axios.get(process.env.VUE_APP_SOCKET_ENDPOINT+'/api/v1/verify', {params: {token: this.token}}).then((resp)=>{
                this.isLogin=true;
                this.token=resp.data.data.token;
                this.user=resp.data.data.user;

                if (this.user.role === 'admin'){
                    this.menu.push({icon: 'mdi-chart-line', title: 'Estadistica', path: '/statics', action: 'openStaticsDialogue'})
                    this.menu.push({icon: 'mdi-account-multiple', title: 'Administrar Usuarios', path: '/users', action: 'openUsersDialogue'});
                }
                this.welcomeDialogue=true;
                this.user.avatar=process.env.VUE_APP_SOCKET_ENDPOINT+this.user.avatar;
            }).catch(()=>{
                this.loginDialogue=true;
                localStorage.removeItem('uid');
            }); 
        }else{
            this.loginDialogue=true;
        }

        setTimeout(()=>{
            ioservice.setupSocketConnection(this.token);
            ioservice.socket.on('server-new-report', (msg)=>{
                if (this.user.settings.newAlerts){
                    const matrix={
                        'Violencia Familiar': 'mdi-home',
                        'Delincuencia': 'mdi-robber',
                        'Accidente de Tránsito': 'mdi-car',
                        'Otro': 'mdi-pencil-circle'
                    };
                    const c=msg.category;
                    msg.category={name: c, icon: matrix[c]};
                    if(msg.photo.trim() === ''){
                        msg.photo=process.env.VUE_APP_SOCKET_ENDPOINT + '/static/sin_imagen.jpg';
                    }

                    this.currentReport=msg;
                    this.currentAddress=this.currentReport.address;
                    this.fullInfoDialogue=true;

                    if (this.user.settings.sound){
                        const audio = new Audio(process.env.VUE_APP_SOCKET_ENDPOINT+'/static/sounds/alert.wav');
                        audio.play();
                    }
                }
            });
        }, 1000);
    }
}
</script>

